export default {
    data(){
        return {
            oldpwd:"",
            newpwd:'',
            confirmpwd:''
        }
    },
    methods: {
        confirmClick(){
            if(this.oldpwd == '') {
                this.$toast('请输入旧密码')
                return false
            }
            if(this.newpwd == '') {
                this.$toast('请输入新密码')
                return false
            }
            if(this.confirmpwd == '') {
                this.$toast('请输入确认密码')
                return false
            }
            if(this.confirmpwd != this.confirmpwd) {
                this.$toast('俩次输入的密码不一致')
                return false
            }
            this.$loading()
            this.$http.geteditpwd({
                old_password:this.oldpwd,
                password:this.newpwd,
                confirm_password:this.confirmpwd
            }).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout(()=>{
                        this.getback()
                    },1500)
                }
            })
        }
    },
}
